<template lang="pug">
  ul
    li(v-for="icon in icons" v-in-viewport.once="true")
      a(:href="icon.url" target="_blank" rel="noopener" aria-label="icon.name")
        svg.icon
          use(xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#icon-' + icon.name")
</template>



<script>
  export default {
    name: 'SocialNetworks',

    data: () => ({
      icons: [
        { name: 'github', url: 'https://github.com/whoisjorge' },
        { name: 'codepen', url: 'https://www.codepen.io/whoisjorge' },
        { name: 'twitter', url: 'https://twitter.com/whois_jorge' },
        // { name: 'linkedin', url: 'https://www.linkedin.com/in/whoisjorge' },
        { name: 'mail', url: 'mailto:email@whoisjorge.me' }
      ]
    })
  }
</script>



<style lang="sass" scoped>
  ul
    display: flex
    justify-content: center
    list-style: none
    padding-left: 1.25rem
    +breakpoint(md)
      padding-left: 0
    li
      color: $orange
      font-size: .8rem
      display: inline-block
      margin: 12px 24px
      opacity: 0
      @for $i from 1 through 4
        &:nth-of-type(#{$i}) 
          &.in-viewport
            animation: (0.5s + ($i - 1) * 0.1s) fadeInUp (3s + ($i - 1) * 0.1s) both
</style>



