<template lang="pug">
  div#preloader
    vue-progress-bar
    BounceSpinner

    div.healtyBrowser
      //- AutoType
      p
        AutoType(:str="'For a better experience, '", :delay='0', :typeSpeed="40", :cleanCursor="true")
        AutoType(:str="'keep your browser up to date!'", :delay='1250', :typeSpeed="40", :cleanCursor="true" v-on:done="typeDone")
</template>


<script>
  import AutoType from '@/components/Type.vue'

  export default {
    name: 'Preloader',

    components: { AutoType },

    methods: {
      typeDone () {
        // End preloading
        this.$Progress.finish()
        setTimeout(() => {
          this.$parent.preloading = false
          // Begin splash animations
          this.$parent.beginAnimations()
        }, 1)
      }
    },

    created () {
      // Start VueProgressBar
      this.$Progress.start()
    },

    mounted () {
      // Disable scrolling
      document.querySelector('html').style.overflow = 'hidden'
    },

    beforeDestroy () {
      setTimeout(() => {
        // Enable scrolling
        document.querySelector('html').style.overflowY = 'auto'
      }, 1700)
    }

  }
</script>



<style lang="sass" scoped>
  #preloader
    background-color: #12082e
    z-index: 1001
    position: fixed
    width: 100%
    height: 100%
    top: 0
    left: 0
    display: flex
    justify-content: center
    align-items: center

    .healtyBrowser
      flex-direction: column
      align-items: center
      align-self: flex-end
      justify-content: flex-start
      padding: 1rem
      p
        color: #cfcfcf
        font-family: 'PT Sans'
        font-size: .625rem
        letter-spacing: 0.05em
        padding: 1rem 0
        // Responsive
        +breakpoint(sm)
          font-size: .7625rem
        +breakpoint(lg)
          font-size: .7625rem
</style>



<style lang="scss">
  /* vue-progressbar */
  .__cov-progress {
    position: absolute!important;
    // background: linear-gradient(330deg, #360670 0%, #db0768 25%, #e23b4a 50%, #faaa54 75%, #23966c 100%)!important;
    background: linear-gradient(to right, #F55D07, #F3BF00)!important;
    height: 0.4em!important;
  }

  /* vue-spinner */
  .v-spinner {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
</style>



