<template lang="pug">
  section#Contact
    //- h2 Do you have an interesting project that makes the web better? I want to help.

    //- ContactForm

    footer(itemscope itemtype="https://schema.org/WPFooter")
      //- Get in Touch!
      div.revealer(v-in-viewport.once="true")
        div.revealer__motion
          div.revealer__block
            div.block
            h4.revealer__title I'm working hard!
      p(v-in-viewport.once="true") Sorry for the inconvenience, the full site is still unavailable!
      


      div.contact(v-in-viewport.once="true")
        b(v-in-viewport.once="true") Don’t hesitate to drop me an e-mail or contact me via social networks!
        SocialNetworks



      //- BackToTop
</template>



<script>
import ContactForm from '@/components/ContactForm.vue'
export default {
  name: 'View_Contact',
  components: { ContactForm }
}
</script>



<style lang="sass" scoped>
  #Contact

    h2
      font-weight: 700
      font-size: 2.8rem
      line-height: 1.2
      padding-bottom: 2rem


  // Get in touch!
  footer
    > *
      padding: .75rem 0

      .revealer
        &__motion
          display: flex
          justify-content: center
          flex-direction: column
          align-items: center
          position: relative
          width: 100%
          height: 100%
          background-position: center center
          background-repeat: no-repeat
          opacity: 0
        &__block
          position: relative
          top: 15px
          overflow: hidden
          .block
            position: absolute
            width: 100%
            height: 100%
            opacity: 0
            animation-duration: 1s
            transition-timing-function: cubic-bezier(0.42, 0, 0.58, 1)
            background: rgba(230, 233, 236, 0.1)
            background: #070707
        &__title
          font-size: 1.9375rem
          font-weight: 700
          position: relative
          white-space: nowrap
          overflow: hidden
          backface-visibility: hidden
          margin: 10px
          padding-right: 20px
          padding-left: 15px
          opacity: 0
          z-index: -1
          transition: opacity .5s
          +unselectable
          // Responsive
          +breakpoint(md)
            font-size: 2.6625rem
          +breakpoint(lg)
            font-size: 3.6625rem
          +breakpoint(xl)
            font-size: 4rem

    p
      position: relative
      top: 50%
      left: 50%
      transform: translate3d(-50%,-50%,0)
      font-size: .75rem
      font-weight: 200
      opacity: 0
      margin-top: 1rem
      padding: .5rem
      text-align: center
      &.in-viewport
        animation: .850s fadeInUp .850s both
      +breakpoint(lg)
        font-size: 1rem



    .contact
      border: 1px solid transparent
      border-image-source: linear-gradient(to right, #F55D07, #F3BF00)
      border-image-slice: 20
      outline: none
      opacity: 0
      width: 90%
      margin: 4rem auto
      &.in-viewport
        animation: 1.5s fadeIn 2s both
    
      b
        color: $orange
        display: block
        position: relative
        top: 50%
        left: 50%
        transform: translate3d(-50%,-50%,0)
        font-size: 1rem
        font-weight: 200
        padding: .5rem
        margin-top: 2rem
        text-align: center
        opacity: 0
        &.in-viewport
          animation: 1.5s fadeInUp 2s both


  // v-in-viewport
  .revealer.in-viewport h4
    transition-delay: .6s
  .revealer.in-viewport .revealer__motion, .revealer.in-viewport h4
    opacity: 1
  .revealer.in-viewport .block
    animation-name: run-block

  // Animation
  @keyframes run-block
    0%
      transform: translateX(-100%)
      opacity: 1
    50%
      transform: translateX(0)
      opacity: 1
    100%
      transform: translateX(100%)
      opacity: 1
</style>



