import Vue from 'vue'

// Custom components
import BounceSpinner from '@/components/BounceSpinner'
import ParticlesJS from '@/components/ParticlesJS'
import SocialNetworks from '@/components/SocialNetworks'
import BackTop from '@/components/BackTop'

//
// Third party stuff
//
// vue-progressbar (https://github.com/hilongjw/vue-progressbar)
import VueProgressBar from 'vue-progressbar'
// vue-in-viewport (https://github.com/BKWLD/vue-in-viewport-directive)
import inViewportDirective from 'vue-in-viewport-directive'
// vue-on-click-outside (https://github.com/kmohrf/vue-on-click-outside)
import { directive as onClickOutside } from 'vue-on-click-outside'
//
// Usage
//
Vue.use(VueProgressBar)
Vue.directive('in-viewport', inViewportDirective)
Vue.directive('on-click-outside', onClickOutside)

// Register Custom Components
const myComponents = {
  ParticlesJS,
  BackTop,
  SocialNetworks,
  BounceSpinner
}
Object.keys(myComponents).forEach((key) => {
  Vue.component(key, myComponents[key])
})



