<!-- Tweaked from the original idea by Yoichi Kobayashi: https://github.com/ykob/shape-overlays -->
<template lang="pug">
  header#navigation(itemscope itemtype="http://schema.org/WPHeader")
    //- Hamburger Menu
    div.hamburger
      //- Lines
      div.hamburger__line.hamburger__line--01
        div.hamburger__line-in.hamburger__line-in--01
      div.hamburger__line.hamburger__line--02
        div.hamburger__line-in.hamburger__line-in--02
      div.hamburger__line.hamburger__line--03
        div.hamburger__line-in.hamburger__line-in--03
      //- Cross
      div.hamburger__line.hamburger__line--cross01
        div.hamburger__line-in.hamburger__line-in--cross01
      div.hamburger__line.hamburger__line--cross02
        div.hamburger__line-in.hamburger__line-in--cross02


    div.content
      //- Site Navigation
      div.global-menu
        nav.global-menu__wrap(itemscope itemtype="http://schema.org/SiteNavigationElement")
          a.global-menu__item(@click="goToSection('About')") About
          a.global-menu__item(@click="goToSection('Work')") Works
          a.global-menu__item(@click="goToSection('Contact')") Contact
      //- Overlay Effect
      svg.shape-overlays(viewBox="0 0 100 100" preserveAspectRatio="none")
        defs
          linearGradient#gradient1(x1="0%" y1="0%" x2="0%" y2="100%")
            stop(offset="0%" stop-color="#00346B")
            stop(offset="100%" stop-color="#001933")
          linearGradient#gradient2(x1="0%" y1="0%" x2="0%" y2="100%")
            stop(offset="0%" stop-color="#110046")
            stop(offset="100%" stop-color="#32004a")
          linearGradient#gradient3(x1="0%" y1="0%" x2="0%" y2="100%")
            stop(offset="0%" stop-color="#07093D")
            stop(offset="100%" stop-color="#141422")
        path.shape-overlays__path
        path.shape-overlays__path
        path.shape-overlays__path
</template>



<script>
import ease from '@/assets/js/easings.js'

export default {
  name: 'Navigation',

  methods: {
    // SPA nav...
    goToSection (sectionID) {
      document.getElementById(sectionID).scrollIntoView()
    }
  },

  mounted () {
    /* eslint-disable */
    class ShapeOverlays {
      constructor(elm) {
        this.elm = elm;
        this.path = elm.querySelectorAll('path');
        this.numPoints = 90;
        this.duration = 900;
        this.delayPointsArray = [];
        this.delayPointsMax = 200;
        this.delayPerPath = 150;
        this.timeStart = Date.now();
        this.isOpened = false;
        this.isAnimating = false;
      }
      toggle() {
        this.isAnimating = true;
        for (var i = 0; i < this.numPoints; i++) {
          this.delayPointsArray[i] = Math.random() * this.delayPointsMax;
        }
        if (this.isOpened === false) {
          this.open();
        } else {
          this.close();
        }
      }
      open() {
        this.isOpened = true;
        this.elm.classList.add('is-opened');
        this.timeStart = Date.now();
        this.renderLoop();
        //
        setTimeout(() => {
          document.querySelector('main').style.opacity = 0;
          document.querySelector('html').style.overflow = 'hidden';
        }, 350)
      }
      close() {
        this.isOpened = false;
        this.elm.classList.remove('is-opened');
        this.timeStart = Date.now();
        this.renderLoop();
        //
        setTimeout(() => {
          document.querySelector('main').style.opacity = 1;
          document.querySelector('html').style.overflowY = 'auto';
        }, 900)
      }
      updatePathClose(time) {
        const points = [];
        for (var i = 0; i < this.numPoints; i++) {
          points[i] = (1 - ease.cubicInOut(Math.min(Math.max(time - this.delayPointsArray[i], 0) / this.duration, 1))) * 100
        }

        let str = '';
        str += (this.isOpened) ? `M 0 0 V ${points[0]}` : `M 0 ${points[0]}`;
        for (var i = 0; i < this.numPoints - 1; i++) {
          const p = (i + 1) / (this.numPoints - 1) * 100;
          const cp = p - (1 / (this.numPoints - 1) * 100) / 2;
          str += `C ${cp} ${points[i]} ${cp} ${points[i + 1]} ${p} ${points[i + 1]} `;
        }
        str += (this.isOpened) ? `V 100 H 0` : `V 0 H 0`;
        return str;
      }
      updatePathOpen(time) {
        const points = [];
        for (var i = 0; i < this.numPoints; i++) {
          points[i] = ease.cubicInOut(Math.min(Math.max(time - this.delayPointsArray[i], 0) / this.duration, 1)) * 100
        }

        let str = '';
        str += (this.isOpened) ? `M 0 0 V ${points[0]} ` : `M 0 ${points[0]} `;
        for (var i = 0; i < this.numPoints - 1; i++) {
          const p = (i + 1) / (this.numPoints - 1) * 100;
          const cp = p - (1 / (this.numPoints - 1) * 100) / 2;
          str += `C ${cp} ${points[i]} ${cp} ${points[i + 1]} ${p} ${points[i + 1]} `;
        }
        str += (this.isOpened) ? `V 0 H 0` : `V 100 H 0`;
        return str;
      }
      render() {
        if (this.isOpened) {
          for (var i = 0; i < this.path.length; i++) {
            this.path[i].setAttribute('d', this.updatePathOpen(Date.now() - (this.timeStart + this.delayPerPath * i)));
          }
        } else {
          for (var i = 0; i < this.path.length; i++) {
            this.path[i].setAttribute('d', this.updatePathClose(Date.now() - (this.timeStart + this.delayPerPath * (this.path.length - i - 1))));
          }
        }
      }
      renderLoop() {
        this.render();
        if (Date.now() - this.timeStart < this.duration + this.delayPerPath * (this.path.length - 1) + this.delayPointsMax) {
          requestAnimationFrame(() => {
            this.renderLoop();
          });
        }
        else {
          this.isAnimating = false;
        }
      }
    }

    (function() {
      const elmToggle = document.querySelector('#navigation');
      const elmHamburger = document.querySelector('.hamburger');
      const gNavItems = document.querySelectorAll('.global-menu__item');
      const elmOverlay = document.querySelector('.shape-overlays');
      const overlay = new ShapeOverlays(elmOverlay);

      elmToggle.addEventListener('click', () => {
        if (overlay.isAnimating) {
          return false;
        }
        overlay.toggle();
        //
        if (overlay.isOpened === true) {
          elmHamburger.classList.add('is-opened-navi');
          for (var i = 0; i < gNavItems.length; i++) {
            gNavItems[i].classList.add('is-opened');
          }
        } else {
          elmHamburger.classList.remove('is-opened-navi');
          for (var i = 0; i < gNavItems.length; i++) {
            gNavItems[i].classList.remove('is-opened');
          }
        }
      });
    }());
  } // mounted()
}
</script>



<style lang="sass" scoped>
  header
    position: absolute
    top: 0
    left: 0
    width: 100%
    display: flex
    align-items: center
    justify-content: space-between
    backface-visibility: hidden

    // Hamburger Menu
    .hamburger
      width: 64px
      height: 64px
      display: block
      position: relative
      cursor: pointer
      position: fixed
      top: 2.25em
      right: 2.25em
      z-index: 110
      pointer-events: auto
      -webkit-tap-highlight-color: rgba(0,0,0,0)
      animation: fadeInDown .7s both
      animation-delay: 4.5s
      top: 0.5em
      right: 0.5em
      transform: scale(0.75)
      +breakpoint(md)
        top: 2.25em
        right: 2.25em


      &__line
        width: 28px
        height: 2px
        overflow: hidden
        position: absolute
        z-index: 10
        transition-duration: 0.6s
        transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1)
        &--01, &--02, &--03
          left: 18px
        &--01
          top: 24.6px
        &--02
          top: 31px
        &--03
          top: 37.4px
        &--cross01, &--cross02
          left: 18px
          top: 31px
        &--cross01
          transform: rotate(45deg)
        &--cross02
          transform: rotate(-45deg)

      &__line-in
        width: 84px
        height: 2px
        position: absolute
        top: 0
        left: 0
        transition-duration: 0.6s
        transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1)
        &:before, &:after
          width: 28px
          height: 2px
          content: ''
          display: block
          position: absolute
          top: 0
          background-color: #ffffff
          transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1)
          transition-property: transform
        &:before
          left: -56px
        &:after
          left: 0
        &--01
          transition-delay: 0.2s
        &--02
          transition-delay: 0.25s
          &:before, &:after
            transition-delay: 0.05s
        &--03
          transition-delay: 0.3s
          &:before, &:after
            transition-delay: 0.1s
        &--cross01, &--cross02
          transform: translateX(-33.3%)
        &--cross01
          transition-delay: 0s
        &--cross02
          transition-delay: 0.05s
          &:before, &:after
            transition-delay: 0.1s

      &:hover
        .hamburger__line-in
          &:before, &:after
            transform: translateX(200%)
          &--01, &--02, &--03
            &:before, &:after
              transition-duration: 1s
          &--cross01, &--cross02
            &:before, &:after
              transition-duration: 0s

      // Opened navigation
      &.is-opened-navi
        .hamburger__line-in
          &--01, &--02, &--03
            transform: translateX(33.3%)
          &--01
            transition-delay: 0s
          &--02
            transition-delay: 0.05s
          &--03
            transition-delay: 0.1s
          &--cross01, &--cross02
            transform: translateX(0)
          &--cross01
            transition-delay: 0.25s
          &--cross02
            transition-delay: 0.3s

        &:hover
          .hamburger__line-in
            &--01, &--02, &--03
              &:before, &:after
                transition-duration: 0s
            &--cross01, &--cross02
              &:before, &:after
                transition-duration: 1s


    .content
      justify-content: center
      min-height: 100vh
      margin: 0 auto
      pointer-events: none
      // Main menu
      .global-menu
        width: 100vw
        height: 100vh
        display: flex
        justify-content: center
        align-items: center
        position: fixed
        top: 0
        left: 0
        pointer-events: none
        z-index: 100
        color: white






      // SVG Overlay FX
      .shape-overlays
        width: 100vw
        height: 100vh
        pointer-events: none
        position: fixed
        top: 0
        left: 0
        .is-opened
          pointer-events: auto
        // Cooolors
        &__path:nth-of-type(1)
          fill: var(--path-fill-1)
        &__path:nth-of-type(2)
          fill: var(--path-fill-2)
        &__path:nth-of-type(3)
          fill: var(--path-fill-3)
        // &__path:nth-of-type(4)
        //   fill: var(--path-fill-4)

</style>







<style lang="scss" scoped>

#navigation {
  --path-fill-1: url(#gradient1);
	--path-fill-2: url(#gradient2);
	--path-fill-3: url(#gradient3);
	// --color-menu-hover: #05ffa1;
  --color-about: #00FFD4;
  --color-works: #F8E81C;
  --color-contact: #F6A623;
}


.global-menu__item {
	color: white;
	font-family: inherit;
	font-size: 2.75rem;
	font-weight: 700;
	opacity: 0;
	transform: translateY(100%);
	pointer-events: none;
	display: block;
	margin: 0.75em 0;
	transition: transform 0.3s, opacity 0.3s;
	transition-timing-function: ease-in;
  @media screen and (min-width: 35.5em) {
    font-size: 4.25rem;
  }
  &:nth-of-type(1) {
    transition-delay: 0.25s;
  }
  &:nth-of-type(2) {
    transition-delay: 0.2s;
  }
  &:nth-of-type(3) {
    transition-delay: 0.15s;
  }
  &:nth-of-type(4) {
    transition-delay: 0.1s;
  }

  &:nth-child(1) {
    &:hover {
      text-shadow:
        3px 3px 0 var(--color-about),
        -1px -1px 0 var(--color-about),
        1px -1px 0 var(--color-about),
        -1px 1px 0 #000,
        1px 1px 0 #000;
    }
    &:after {
      background-color: var(--color-about);
    }
  }
  &:nth-child(2) {
    &:hover {
      text-shadow:
        3px 3px 0 var(--color-works),
        -1px -1px 0 var(--color-works),
        1px -1px 0 var(--color-works),
        -1px 1px 0 var(--color-works),
        1px 1px 0 var(--color-works);
    }
    &:after {
      background-color: var(--color-works);
    }
  }
  &:nth-child(3) {
    &:hover {
      text-shadow:
        3px 3px 0 var(--color-contact),
        -1px -1px 0 var(--color-contact),
        1px -1px 0 var(--color-contact),
        -1px 1px 0 #000,
        1px 1px 0 #000;
    }
    &:after {
      background-color: var(--color-contact);
    }
  }

  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -4px;
    width: 0;
    height: 3px;
    transition: width ease .3s;
  }
  &:hover {
    &:after {
      width: 20%
    }
  }

  // Opened
  &.is-opened {
    opacity: 1;
    transform: translateY(0) rotate(0);
    pointer-events: auto;
    cursor: pointer;
    transition-timing-function: ease;
    transition-duration: 0.8s;
    &:nth-of-type(1) {
    transition-delay: 1s;
    }
    &:nth-of-type(2) {
      transition-delay: 1.1s;
    }
    &:nth-of-type(3) {
      transition-delay: 1.2s;
    }
    &:nth-of-type(4) {
      transition-delay: 1.3s;
    }
  }

}


</style>



