<template lang="pug">
  div#app
    //- Preloader
    transition(name="fade")
      Preloader(v-if="preloading")


    //- General usage graphics
    SVGicons


    //-----------------------
    Navigation
    router-view
    //- Footer
    //-----------------------


    //- Canvas
    ParticlesJS(v-if="!preloading" v-once)
</template>



<script>
  import SVGicons from '@/components/SVGicons'
  import Preloader from '@/components/Preloader'
  import Navigation from '@/components/Navigation'

  export default {
    name: 'App',

    data: () => ({
      preloading: true
    }),

    components: {
      SVGicons,
      Preloader,
      Navigation
    },

    mounted () {
      this.boot()
    },

    methods: {
      boot () {
        // Disable app visibility
        document.querySelector('main').classList.add('is-disabled')
      },

      beginAnimations () {
        // Enable app visibility
        document.querySelector('main').classList.remove('is-disabled')
      }
    }

  }
</script>



