import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/views/Index'
// import Test from '@/views/sections/Work'

Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      component: Index
    },
    // {
    //   path: '/test',
    //   component: Test
    // },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



